*, *::after, *::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font: normal normal normal 1rem/1.6 Nunito Sans, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e6e6ea;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  text-align: center;
  padding: 30px 0;
  background-color: #fed766;
}

.header>h1{
  font-size: 3rem;
}

.header>ul {
  list-style-type: none;
  padding-left: 0;
}

.header>ul>li {
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid #000;
  margin-left: -1px;
  width: 160px;
}

.header>ul>li:first-child {
  border-radius: 3px 0px 0px 3px;
}

.header>ul>li:last-child {
  border-radius: 0px 3px 3px 0px;
}

.header>ul>li:not(.active):hover {
  background-color: #333;
  color: #fff;
  cursor: pointer;
}

.header>ul>li.active {
  background-color: #000;
  color: #fff;
}

.header>ul>li.active:hover {
  cursor: default;
}

.header>div>button {
  background-color: #2ab7ca;
  color: #fff;
  border: none;
  outline: none;
  padding: 10px 15px;
  margin: 0 auto;
  border-radius: 3px;
  font-size: 1rem;
  margin-top: 15px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 4px 15px 0px;
}

.header>div>button:hover {
  cursor: pointer;
}

.header>div>button:active {
  box-shadow: none;
}

@media (max-width: 1400px) {
  .header {
    padding: 5px 0 25px;
  }
  .header>h1{
    font-size: 2rem;
    margin-bottom: 0px;
  }
  .header>ul>li {
    padding: 5px;
  }
  .header>div>button{
    padding: 8px 12px;
  }
}

@media (max-width: 575px) {
  .header {
    padding: 20px 0;
  }
  .header>h1{
    font-size: 2rem;
  }
  .header>ul>li {
    border-radius: 3px;
    margin-top: 10px;
  }
}
.main {
  padding: 0px 150px 30px 150px;
  background-color: #000;
}

.main>.info {
  color: #fff;
  text-align: center;
  padding: 30px 0;
  font-size: 1.5rem;
}

/* Draw winning line*/

.main>.board {
  position: relative;
}

.main>.board::after {
  content: '';
  position: absolute;
  background-color: #2ab7ca;
  transition: 0.7s;
  -webkit-transition: 0.7s;
  -moz-transition: 0.7s;
  -o-transition: 0.7s;
}

/* Horizonal Lines */

.main>.board.h::after {
  width: 0%;
  height: 3px;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.main>.board.h.full::after {
  width: 100%;
}

.main>.board.h0::after {
  top: calc(100% / 6);
}

.main>.board.h1::after {
  top: 50%;
}

.main>.board.h2::after {
  top: calc(5 * 100% / 6);
}

/* Vertical Lines */

.main>.board.v::after {
  height: 0%;
  width: 3px;
  top: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.main>.board.v.full::after {
  height: 100%;
}

.main>.board.v0::after {
  left: calc(100% / 6);
}

.main>.board.v1::after {
  left: 50%;
}

.main>.board.v2::after {
  left: calc(5 * 100% / 6);
}

/* Diagonal Lines */

.main>.board.d0::after {
  height: 0%;
  width: 3px;
  left: 0;
  top: 0;
  -webkit-transform: rotateZ(-45deg);
          transform: rotateZ(-45deg);
  -webkit-transform-origin: 50% 0;
          transform-origin: 50% 0;
  transition: height 0.7s;
  -webkit-transition: height 0.7s;
  -moz-transition: height 0.7s;
  -o-transition: height 0.7s;
}

.main>.board.d0.full::after {
  height: 140%;
}

.main>.board.d1::after {
  height: 0%;
  width: 3px;
  right: 0;
  top: 0;
  -webkit-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
  -webkit-transform-origin: 50% 0;
          transform-origin: 50% 0;
  transition: height 0.7s;
  -webkit-transition: height 0.7s;
  -moz-transition: height 0.7s;
  -o-transition: height 0.7s;
}

.main>.board.d1.full::after {
  height: 140%;
}

/* Draw winning line*/

.main>.board>.board-row {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
}

.main>.board>.board-row>.cell {
  flex: 1 1;
  padding-top: calc(100% / 6 - 2.9rem);
  padding-bottom: calc(100% / 6 - 2.9rem);
  font-size: 5rem;
  line-height: 5.7rem;
  background-color: transparent;
  border: none;
  outline: none;
  border: 2px solid #fff;
  margin-left: -2px;
  margin-top: -2px;
}

.main>.board>.board-row>.cell.done {
  color: #fff;
}

.main>.board.full>.board-row>.cell {
  cursor: default;
}

.main>.board:not(.full)>.board-row>.cell:not(.done):hover {
  cursor: pointer;
  background-color: #111;
  color: #111;
}

.main>.board>.board-row>.cell.cell-0,
.main>.board>.board-row>.cell.cell-1,
.main>.board>.board-row>.cell.cell-2 {
  border-top: none;
}

.main>.board>.board-row>.cell.cell-0,
.main>.board>.board-row>.cell.cell-3,
.main>.board>.board-row>.cell.cell-6 {
  border-left: none;
}

.main>.board>.board-row>.cell.cell-6,
.main>.board>.board-row>.cell.cell-7,
.main>.board>.board-row>.cell.cell-8 {
  border-bottom: none;
}

.main>.board>.board-row>.cell.cell-2,
.main>.board>.board-row>.cell.cell-5,
.main>.board>.board-row>.cell.cell-8 {
  border-right: none;
}

@media (max-width: 1400px) {
  .main {
    padding: 0px 120px;
  }
  .main>.info {
    padding: 20px 0;
    font-size: 1.2rem;
  }
  .main>.board>.board-row>.cell {
    padding-top: calc(100% / 6 - 1.7rem);
    padding-bottom: calc(100% / 6 - 1.7rem);
    font-size: 3rem;
    line-height: 3.3rem;
  }
}

@media (max-width: 575px) {
  .main {
    padding: 0px 50px;
  }
}
.footer {
  text-align: center;
  padding: 10px;
  background-color: #000;
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.footer a {
  position: relative;
  color: #fff;
  text-decoration: none;
}

.footer a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: -4px;
  background-color: #fff;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  -o-transition: all 0.3s ease-in-out 0s;
}

.footer a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
.app {
  width: 100%;
  max-width: 640px;
  min-width: 360px;
  margin: 50px auto;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 5px 30px 0px;
}

@media (max-width: 1400px) {
  .app {
    max-width: 500px;
    margin: 20px auto;
  }
}

@media (max-width: 575px) {
  .app {
    margin-top: 0px;
  }
}
